@media only screen and (max-width: 768px) {
  .FooterWrapper {
    width: 102.7%;
    height: 25em;
    background-color: black;
    box-sizing: border-box;
    position: relative;
  }
  .FootHeadear{
    width: 5em;
    box-sizing: border-box;
    position: relative;
    left: 2em;
    
  }
  .FootLogo{
   position: absolute;
   font-size: 1.5em;
   top: -1em;
   left: 2.5em;
   box-sizing: border-box;
  }
  .FootLogo > p{
    position: absolute;
    top: 5.3em;
    left: 1em;
    font-size: 0.5em;
   }
   .Footline{
    display: none;
   }
   .FNewsLetter{
    height: auto;
    position: relative;
    left: 0em;
    top: -18em;
    box-sizing: border-box;
   }
   .FNewsLetter > input{
    width: 19em;
    height: 3.5em;
    border: none;
    position: absolute;
    left: 1em;
    border-radius: 15px;
    pointer-events:fill;
   }
   .FNewsLetter > input:focus{
    outline: none;
   }
   .FNewsLetter > button{
    position: absolute;
    left: 20.5em;
    width: 8em;
    height: 3.65em;
    border-radius: 15px;
    cursor: pointer;
   }
   .Subbutton:hover {
    background-color: #276ecf;
    color: #f6f6f6; 
  }
   .CopyrightWrapper{
    width: 23em;
    height: 2em;
    position: relative;
    left: 0em;
    top: -2em;
    box-sizing: border-box;
   }
   .CopyrightWrapper >p{
    height: 2em;
    position: absolute;
    top: -2em;
    box-sizing: border-box;
   }
   .SocailProfileWrapper{
    width: 15em;
    box-sizing: border-box;
    display: flex;
    position: absolute;
    top: -5em;
    left: 5em;
   }
   .SocailProfileWrapper > p{
    font-size: 1.2em;
    position: absolute;
    left: 2em;
   }
  .FooterSocial{
    width: 40px;
    height: 40px;
    padding: 2px;
    /* box-sizing: border-box; */
    position: relative;
    top: 0.5em;
    left: -1em;
    margin-left: 1em;
  }
  .FooterSocial > img{
    width: 100%;
    height: 100%;
  }
  #twitter>img{
    border-radius: 5px;
    background-color: wheat;
  }
  .FooterOptionWrapper{
    display: grid;
    grid-template-columns: auto auto auto;
    grid-template-areas: "line1"
                          "line2"
                          "line3";
    width: 19.5em;
    height: 7em;
    position: relative;
    left: 1em;
    top: -30.5em;
    box-sizing: border-box;
    font-size: 0.8em;
  }
  .OptionLine{
    position: relative;
    top: 1em;
  }
  .OptionLine1{
    grid-area: line1;
    position: absolute;
    top: 2.5em;
    left: -1em;
  }
  .OptionLine2{
    grid-area: line2;
    width: 28em;
    position: absolute;
    top: 2em;
    left: -1em;
    margin-top: 0.9em;
  }
  .OptionLine3{
    grid-area: line3;
    position: absolute;
    top: 1.5em;
    margin-top: 1.5em;
    left: -1em;
  }
  .LinkWrapper{
    text-decoration: none;
    margin:1.5em;
  }
  .BackTopWrapper{
    display: none;
    font-weight: bolder;
  }
  .BackTopWrapper > div{
    transform: rotate(90deg);
    position: relative;
    top: 80px;
    cursor: pointer;
  }
  .VersionWrap{
    display: none;
    
  }
  .FErrorMessage{
    width: 20em;
    color: red;
    position: absolute;
    top: 2.9em;
    left: -1.5em;
  }
  .FThanksMessage{
    color: green;
    width: 15em;
    position: absolute;
    top: 2.9em;
    left: -1.5em;
  }
}